.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/*------------------------------------|
|	Content Wrap                        |
|------------------------------------*/
/*---- Limits width of content, adds side padding, & allows background colours to extend to screen edge ----*/
.l-constrained {
  position: relative;
  max-width: 1600px;
  margin: 0 auto;
  padding-right: 2.5%;
  padding-left: 2.5%;
  padding-right: calc(2.5% + 15px);
  padding-left: calc(2.5% + 15px);
}

.l-constrained--grid {
  padding-right: 2.5%;
  padding-left: 2.5%;
}

/*------------------------------------|
|	Grid                                |
|------------------------------------*/
.grid-container {
  list-style-type: none;
  text-align: center;
  z-index: 2;
}
.grid-container:after {
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
  content: " ";
}

.grid-full .grid-item__inner {
  display: block;
  width: 100%;
}
@media (min-width: 480px) {
  .grid-full .grid-item__inner {
    width: 99%;
    width: calc(100% - 15px);
    margin-left: 7.5px;
    margin-right: 7.5px;
  }
}

.grid-quarter .grid-item__inner {
  display: block;
  width: 100%;
}
@media (min-width: 480px) {
  .grid-quarter .grid-item__inner {
    display: inline-block;
    width: 48.4%;
    width: calc(50% - 15px);
    margin-left: 7.5px;
    margin-right: 7.5px;
  }
}
@media (min-width: 960px) {
  .grid-quarter .grid-item__inner {
    width: 23.4%;
    width: calc(25% - 15px);
  }
}

