  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 5;
}

/*------------------------------------|
|	Sliders                             |
|------------------------------------*/
/*------------------------------------|
|	Breadcrumbs                         |
|------------------------------------*/
.breadcrumbs {
  text-align: center;
  text-transform: uppercase;
}
.breadcrumbs li {
  display: inline-block;
  vertical-align: middle;
}

.breadcrumbs .is-active a {
  color: red;
  font-weight: 700;
}

/*------------------------------------|
|	Icons                               |
|------------------------------------*/
.icn {
  display: inline-block;
  vertical-align: bottom;
}

.icn--16 {
  height: 16px;
  width: 16px;
}

.icn--sprite {
  background: url("../img/icon/sprite.png") no-repeat;
  line-height: 0;
}

.icn--sprite-fb {
  background-position: 20px 20px;
}

.icn--sprite-li {
  background-position: 0px 0px;
}

/*------------------------------------|
|	Modal                               |
|------------------------------------*/
.modal--nugget {
  position: fixed;
  display: none;
  top: 10%;
  left: 10%;
  width: 80%;
  height: 80%;
  z-index: 9999999;
}

.modal--nugget.is-visible {
  display: block;
}

/*------------------------------------|
|	Nav Links                           |
|------------------------------------*/
.nav__container {
  float: right;
  display: inline-block;
}
@media (max-width: 959.99px) {
  .nav__container {
    position: absolute;
    left: 100%;
    right: -100%;
    top: 88px;
    bottom: 0;
    transition: left .6s ease-in, right .6s ease-in;
    background-color: rgba(2, 51, 57, 0.9);
    z-index: 4;
    transition-delay: .4s;
  }
}

.nav li {
  display: inline-block;
  line-height: 45px;
}
.nav a {
  font-size: 16px;
  font-size: 1rem;
  line-height: 16px;
  line-height: 1rem;
}

.nav--header li {
  border-radius: 100px;
  margin: 0 7.5px;
}
.nav--header a {
  padding: 7.5px 4px;
  border-radius: 30px;
}
@media (min-width: 1124px) {
  .nav--header li {
    margin: 0 15px;
  }
  .nav--header a {
    padding: 7.5px 13px;
  }
}
@media (max-width: 959.99px) {
  .nav--header {
    position: relative;
    left: 100%;
    transition: left .6s ease-in;
    transition-delay: 0s;
    padding: 20px;
  }
  .nav--header a {
    color: #fff;
    font-size: 18px;
    font-size: 1.125rem;
    line-height: 18px;
    line-height: 1.125rem;
    padding: 7.5px 13px;
  }
  .nav--header li {
    display: block;
    line-height: 55px;
  }
}

.nav--footer li {
  margin: 0 13.5px;
}
.nav--footer a {
  line-height: 50px;
}

.nav--header .is-active a {
  background-color: red;
  color: #fff;
}

@media (max-width: 959.99px) {
  .is-menu-open .nav__container {
    left: 0;
    right: 0;
    transition: left .6s ease-out, right .6s ease-out;
    transition-delay: 0s;
  }
  .is-menu-open .nav {
    left: 0;
    transition: left .6s ease-out;
    transition-delay: .4s;
  }
}
/*------------------------------------|
|	404, Not Found                      |
|------------------------------------*/
/*------------------------------------|
|	Social                              |
|------------------------------------*/
.social li {
  display: inline-block;
  margin-right: 18px;
}
.social li:last-child {
  margin-left: 0;
}
.social i {
  color: #fff;
  font-size: 20px;
  font-size: 1.25rem;
}

/*------------------------------------|
|	Type - Titles                       |
|------------------------------------*/
.th-block, .th-block-a, .th-block-b {
  font-family: "font-name", sans-serif;
  text-transform: uppercase;
}

.th-block-a {
  font-size: 28px;
  font-size: 1.75rem;
  line-height: 31px;
  line-height: 1.9375rem;
  letter-spacing: 0.35px;
  letter-spacing: 0.02188rem;
}
@media (min-width: 840px) {
  .th-block-a {
    font-size: 45px;
    font-size: 2.8125rem;
    line-height: 50px;
    line-height: 3.125rem;
    letter-spacing: 0.5625px;
    letter-spacing: 0.03516rem;
  }
}

.th-block-b {
  font-size: 21px;
  font-size: 1.3125rem;
  line-height: 31px;
  line-height: 1.9375rem;
  letter-spacing: 0.2625px;
  letter-spacing: 0.01641rem;
}
@media (min-width: 840px) {
  .th-block-b {
    font-size: 35px;
    font-size: 2.1875rem;
    line-height: 50px;
    line-height: 3.125rem;
    letter-spacing: 0.4375px;
    letter-spacing: 0.02734rem;
  }
}

